import styles from "./cart.module.css";
import { Link } from "react-router-dom";

export const OrderComplete = () => {
    const date = new Date();
    const week = new Array('', '', '', '', '', '', '');
    return(
        <div className={styles.orderComplete}>
            <h1 className={styles.orderCompleteH1}> !</h1>
            <p className={styles.orderCompleteP}>
                 : {date.toLocaleString()} [{week[date.getDay()]}]<br/>
                 1~2  <b className={styles.orderCompleteB}>[{week[date.getDay() + 2]}]  </b>
            </p>
            <p className={styles.orderCompleteP}>
            <Link to="/">
                <button className={styles.btn_submit}>
                    
                </button>
            </Link>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Link to="/cart">
                <button className={styles.btn_submit}>
                    
                </button>
            </Link>
            </p>
        </div>
    )
}